import React, { useState } from 'react';
import { FaLock } from 'react-icons/fa';
import { BiHome } from 'react-icons/bi';

import pocdquestions from './pocd';
import icdwaquestions from './icdwa';
import daaquestions from './daa';
import cnquestions from './cn';
import sequestions from './se';
import wpquestions from './wp';

export default function App() {
	const [state, setState] = useState(null);
	const [current, setCurrent] = useState(0);
	const [score, setScore] = useState(false);
	const [correct, setCorrect] = useState(0);

	const handleClick = (isCorrect) => {
		if (isCorrect === true) {
			setCorrect(correct + 1);
		}
		if (current + 1 < state.length) {
			setCurrent(current + 1);
		} else {
			setScore(true);
		}
	}
	if (state) {
		return (
			<div className='app'>
				<button type='reset' id='home' onClick={() => window.location.reload()}><BiHome /></button>
				{/* HINT: replace "false" with logic to display the 
      score when the user has answered all the questions */}
				{score ? (
					<div className='score-section'>You scored {correct} out of {state.length}</div>
				) : (
						<>
							<div className='question-section'>
								<div className='question-count'>
									<span>Question {current + 1}</span>/{state.length}
								</div>
								<div className='question-text'>{state[current].questionText}</div>
							</div>
							<div className='answer-section'>
								{state[current].answerOptions.map((opt) => {
									return <>
										<button onClick={() => handleClick(opt.isCorrect)}>{opt.answerText}</button>
									</>
								})}
							</div>
						</>
					)}
			</div>
		);
	}

	return <>
		{/* <button type='reset' onClick={() => location.reload()}>HOME</button> */}
		<h1><i>Choose a subject</i></h1>


		<button type='button' className='subject' disabled onClick={() => setState(sequestions)}>Software Engineering <FaLock style={{ marginLeft: '35%' }} /></button>
		<br />
		<button type='button' className='subject' disabled onClick={() => setState(daaquestions)}>Algorithms <FaLock style={{ marginLeft: '62%' }} /></button>
		<br />
		<button type='button' className='subject' disabled onClick={() => setState(cnquestions)}>Computer Networks <FaLock style={{ marginLeft: '38%' }} /></button>
		<br />
		<button type='button' className='subject' disabled onClick={() => setState(wpquestions)}>Web Programming <FaLock style={{ marginLeft: '42%' }} /></button>
		<br />
		<button type='submit' className='subject' onClick={() => setState(pocdquestions)}>Compiler Design</button>
		<br />
		<button type='button' className='subject' disabled onClick={() => setState(icdwaquestions)}>Indian Constitution <FaLock style={{ marginLeft: '40%' }} /></button>

		<p><i>rest of the subjects to be unlocked soon</i></p>
		<a href="https://t.co/NnzHqWoGp4?amp=1"><p className='pride'>Enjoyed em Let's <a href="https://t.co/NnzHqWoGp4?amp=1
"> Connect ðŸ¤ž</a></p></a>

	</>
}
